import InputField, { FileUploader } from "./js/forms";
import DropDown from "./js/dropdown";
import { init } from "./js/carousel";
import sidenav from "./js/sidenav";
import initTabs from "./js/tabs";
import Toast from "./js/toaste";
import tippy from "tippy.js";
import moment from "moment";

// main app styling
import "./scss/index.scss";
import "tippy.js/dist/tippy.css";

InputField.init(document.querySelectorAll(".field__input"));
DropDown.init(document.querySelectorAll(".dropdown"));

// initializing the carousel
init(document.querySelectorAll(".carousel-container"));

// get language switcher element

class Modal {
  constructor(toggle) {
    this.toggle = toggle;
    this.modal = document.querySelector(`#${toggle.dataset.modelId}`);
    this.modelWrapper = this.modal.querySelector(".model__wrapper");
    this.modelClose = this.modal.querySelector(".model__close");
    this.toggle.addEventListener("click", this.open);
    this.modal.addEventListener("click", this.close);
    this.modelWrapper.addEventListener("click", this.stopPropagation);
    this.modelClose.addEventListener("click", this.close);
  }

  open = (e) => {
    e.preventDefault();
    this.modal.classList.add("show");
  };

  close = (e) => {
    e.preventDefault();
    this.modal.classList.remove("show");
  };

  stopPropagation = (e) => {
    e.stopPropagation();
  };
}

document
  .querySelectorAll(".modal-toggle")
  .forEach((toggle) => new Modal(toggle));

// side nav
sidenav.init();

// tabs
initTabs();

// tooltips
tippy("[data-tippy-content]");

// Toasts
window.toast = Toast;
window.FileUploader = FileUploader;

// custom birth date input
const customDobField = document.querySelectorAll(".js-field-dob");
[...customDobField].forEach((element) => {
  const local = document.documentElement.lang || "en";
  moment.locale(local);

  const fields = {
    year: {
      max: new Date().getFullYear(),
      min: 1930,
    },
    day: {
      max: 31,
      min: 1,
    },
    month: {},
  };

  fields.day.data = Array.from({ length: 31 }, (_, idx) => idx + 1);
  fields.month.data = moment.months();
  fields.year.data = Array.from(
    { length: fields.year.max - fields.year.min },
    (_, idx) => fields.year.max - idx
  );

  // create three select input (Day, Month, Year)
  const DaySelectInput = document.createElement("select");
  const MonthSelectInput = document.createElement("select");
  const YearSelectInput = document.createElement("select");

  DaySelectInput.name = "day";
  MonthSelectInput.name = "month";
  YearSelectInput.name = "year";

  // populate them
  [DaySelectInput, MonthSelectInput, YearSelectInput].forEach((input) => {
    console.log(fields[input.name], input.name);
    const { data } = fields[input.name];
    data.forEach((value) => {
      const optionElement = document.createElement("option");
      optionElement.value = value;
      optionElement.innerText = value;
      input.appendChild(optionElement);
    });

    //todo : add eventListener  for validation

    // add to element
    element.appendChild(input);
  });
});

