class InputField {
  constructor(input) {
    this.input = input;
    this.parent = input.parentElement;
  }

  static init(domElements = []) {
    domElements.forEach((element, i) => {
      console.log(element);
      if (element.getAttribute("type") == "password")
        new PasswordField(element);
      else new InputField(element);
    });
  }
}

class PasswordField extends InputField {
  isVisible = false;

  constructor(input) {
    super(input);
    this.toggleButton = this.parent.querySelector(".field-password__toggle");
    if (this.toggleButton)
      this.toggleButton.addEventListener("click", this.onClick);
  }

  onClick = (e) => {
    e.preventDefault();
    this.isVisible = !this.isVisible;
    this.toggleVisibility();
  };

  toggleVisibility = () => {
    this.toggleButton.classList.toggle("active");
    this.toggleButton.innerText = this.isVisible
      ? "visibility_off"
      : "visibility";
    this.input.setAttribute("type", this.isVisible ? "text" : "password");
  };
}

const DEFAULT_OPTIONS = {
  allowedTypes: ["docx", "doc", "pdf", "word", "png", "jpg", "jpeg", "mp4"],
  maxFileSize: 1024 * 1024 * 20, // 20 MB max
  onSuccess: function (data, form) {
    console.log("onSuccess");
  },
  onError: function (err, form) {
    console.log("onError");
  },
  onProgress: function (progress, form) {
    console.log("onProgress");
  },
  onChange: function (file, form) {
    console.log("onChange");
  },
};

export function FileUploader(form, options = {}) {
  this.form = form;
  this.input = form.querySelector("input[type='file']");
  this.progressBar = form.querySelector(".file__progress-bar");
  this.progressSpinner = form.querySelector(".file__progress-spinner");
  this.fileName = form.querySelector(".file__name");

  options = { ...DEFAULT_OPTIONS, ...options };
  const {
    allowedTypes,
    maxFileSize,
    onError,
    onSuccess,
    onProgress,
    onChange,
  } = options;

  const visibility = (element, value = "visible") => {
    element.style.visibility = value;
  };

  this._handleUpload = (e) => {
    console.log("file changed");
    const files = e.target.files;

    // check for empty files
    if (!files.length) return;
    const file = files[0];

    // check file extension
    const fileExtension = file.name.substr(file.name.lastIndexOf(".") + 1);
    if (!allowedTypes.includes(fileExtension)) {
      alert(`File type should be one of these : ${allowedTypes.join(",")}`);
      return;
    }

    // check file size
    if (file.size > maxFileSize) {
      alert(
        `File size should be at most ${Math.round(
          maxFileSize / (1024 * 1024)
        )} MB`
      );
      return;
    }

    this.fileName.innerText = file.name;

    // calc onChange cb
    onChange(file, this);
    // start loading
    visibility(this.progressSpinner, "visible");

    // ok
    const data = new FormData();
    data.append("file", file);

    // append csrf data
    [...this.form.querySelectorAll("input[type='hidden']")].forEach((input) => {
      data.append(input.name, input.value);
    });

    const uploadURL = options.url;
    const request = new XMLHttpRequest();
    request.open("POST", uploadURL);

    request.upload.addEventListener("progress", (e) => {
      const progress = (e.loaded / e.total) * 100;
      onProgress(progress, this);
    });

    request.addEventListener("load", (e) => {
      visibility(this.progressSpinner, "hidden");
      const response = JSON.parse(request.response);
      if (request.status >= 400 || request.status >= 500) {
        onError(response, this);
      } else {
        onSuccess(response, this);
      }
    });

    request.send(data);
  };

  this.input.addEventListener("change", this._handleUpload);
}

export default InputField;
